import {combineReducers} from 'redux'
import {authConstants} from '../actions/constants'

const initState={
    email:"",
    password:""
}

export const authReducer=(state=initState,action)=>{
    switch(action.type){
        case authConstants.LOGIN_REQUEST:{
            state={...state,
                ...action.payload
            }
        }
        default: return state
    }
}

